import React from 'react';
import { Link } from "react-router-dom";
import styled from 'styled-components';

const Item = styled.div`
  width: 240px;
  margin-top: 10px;
  position: relative;
`
const ItemImg = styled.img`
  max-width: 100%;

  ${Item}:hover & {
    opacity: .3;
  }
`
const ItemOverlay = styled.div`
  position: absolute;
  top: 40%;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  color: #3e3e3e;
  font-family: 'Oswald';
  font-size: 24px;

  ${Item}:hover & {
    opacity: 1;
  }
`

const GalleryItem = ({
  artworkId,
  title,
  slug,
  cover,
}) => {
  const { file: coverImgFile } = cover.fields;
  const { url: coverImgUrl } = coverImgFile;
  return (
    <Link to={`/artworks/${artworkId}`}>
      <Item>
        <ItemImg src={coverImgUrl} alt={slug}/>
        <ItemOverlay>
          <div>
            {title}
          </div>
        </ItemOverlay>
      </Item>
    </Link>
  )
}

export default GalleryItem;
