import React from 'react';
import { compose, lifecycle, withStateHandlers } from "recompose";
import styled from 'styled-components'

import ContentClient from 'lib/content-client'

const ArtworkWrapper = styled.div`
  display: flex;
  flex-direction: column;
  margin: 0 20px;
`
const ArtworkItem = styled.div`
  margin-top: 15px;
`
const ArtworkImg = styled.img`
  max-width: 780px;
`

const Artwork = ({ artworkContent }) => {
  if (!artworkContent) {
    return <div>...</div>
  }

  const { collections: imgCollections } = artworkContent.fields;

  return (
    <ArtworkWrapper>
      {imgCollections && 
        imgCollections.map(imgCollection => {
          const { 
            fields: { file: imgFile } = {}, 
            sys: { id: imgId } = {},
          } = imgCollection;
          const { url: imgUrl } = imgFile

          return (
            <ArtworkItem key={imgId}>
              <ArtworkImg src={imgUrl} alt='artwork' />
            </ArtworkItem>
          );
        })
      }
    </ArtworkWrapper>
  )
}

const enhance = compose(
  withStateHandlers(
    { artworkContent: null },
    {
      setArtworkContent: () => (payload) => ({
        artworkContent: payload,
      })
    }
  ),
  lifecycle({
    componentDidMount() {
      const { match, setArtworkContent } = this.props
      const { artwork } = match.params
      ContentClient.getEntry(artwork).then(entry => {
        setArtworkContent(entry)
      })
    }
    
  })
)(Artwork)

export default enhance;