import React from 'react';
import { compose, lifecycle, withStateHandlers } from "recompose";
import styled from 'styled-components'

import { GALLERY_ENTRY_ID } from "constants/contentful";
import ContentClient from 'lib/content-client'

import GalleryCol from './GalleryCol';

const GalleryWrapper = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
`

const Gallery = ({ galleryContent }) => {
  if(!galleryContent) {
    return <div>...</div>
  }
  const { columns } = galleryContent.fields
  return (
    <GalleryWrapper>
      {columns.map((column) => {
        const { id, artworks } = column.fields;
        return <GalleryCol key={id} artworks={artworks} />
      })}
    </GalleryWrapper>
  )
};

const enhance = compose(
  withStateHandlers(
    { galleryContent: null },
    {
      setGalleryContent: () => (payload) => ({
        galleryContent: payload,
      })
    }
  ),
  lifecycle({
    componentDidMount() {
      ContentClient.getEntry(GALLERY_ENTRY_ID, {include: 2}).then(entry => {
        this.props.setGalleryContent(entry)
      });
    }
  })
)(Gallery)

export default enhance;